  border-color: #3498db;
  color: #3498db;
}
.button--primary:hover,
.navbar__button--primary:hover,
.search__submit--primary:hover,
.navbar__button:hover,
.button--primary:active,
.navbar__button--primary:active,
.search__submit--primary:active,
.navbar__button:active,
.button--primary:focus,
.navbar__button--primary:focus,
.search__submit--primary:focus,
.navbar__button:focus {
  border-color: #217bb8;
  color: #217bb8;
}
.button--primary-fill,
.navbar__button--primary-fill,
.search__submit--primary-fill,
.search__submit {
  border-color: #3498db;
  background-color: #3498db;
  color: #fff;
}
.button--primary-fill:hover,
.navbar__button--primary-fill:hover,
.search__submit--primary-fill:hover,
.search__submit:hover,
.button--primary-fill:active,
.navbar__button--primary-fill:active,
.search__submit--primary-fill:active,
.search__submit:active,
.button--primary-fill:focus,
.navbar__button--primary-fill:focus,
.search__submit--primary-fill:focus,
.search__submit:focus {
  border-color: #217bb8;
  background-color: #217bb8;
  color: #fff;
}
.button--secondary,
.navbar__button--secondary,
.search__submit--secondary {
  border-color: #2ecc71;
  background-color: #2ecc71;
  color: #fff;
}
.button--secondary:hover,
.navbar__button--secondary:hover,
.search__submit--secondary:hover,
.button--secondary:active,
.navbar__button--secondary:active,
.search__submit--secondary:active,
.button--secondary:focus,
.navbar__button--secondary:focus,
.search__submit--secondary:focus {
  border-color: #27ad60;
  background-color: #27ad60;
  color: #fff;
}
.button--large,
.navbar__button--large,
.search__submit--large {
  padding: 0 20px;
  font-size: 14px;
  height: 43px;
  padding-bottom: 2px;
}
.container,
.search__form,
.footer__primary {
  lost-center: 980px 20px;
}
a {
  color: rgba(0,0,0,0.48);
  text-decoration: none;
  outline: 0;
  transition: all 0.2s ease-in-out;
}
a:hover,
a:focus {
  color: #3498db;
}
body {
  min-height: 100vh;
  background-color: #f5f5f5;
}
.master__aside {
  lost-column: 1/4 2 30px;
}
.master__main {
  lost-column: 3/4 2 30px;
}
.shadow,
.navbar,
.search__form,
.city-box,
.category-box__item,
.suggestion__item,
.application {
  box-shadow: 0 0 30px rgba(0,0,0,0.07);
  background-color: #fff;
  border-radius: 2px;
}
.navbar {
  position: relative;
  z-index: 999;
  background-color: #fff;
}
.navbar .container {
  height: 60px;
  display: flex;
  align-items: center;
}
.navbar__brand {
  line-height: 0;
}
.navbar__brand img {
  height: 30px;
}
.navbar__list {
  padding-left: 0;
  list-style: none;
  margin-bottom: 0;
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: baseline;
  font-size: 13px;
}
.navbar__item {
  margin-left: 20px;
}
.hero {
  position: relative;
  background-image: url("../img/header.png");
  padding: 40px 0 90px;
  overflow: hidden;
}
.hero .container {
  position: relative;
}
.hero__heading {
  text-align: center;
  margin: 0;
  color: #fff;
}
h1.hero__heading {
  font-weight: bold;
  font-size: 30px;
  letter-spacing: 1.1px;
  margin-bottom: 7px;
}
h2.hero__heading {
  font-size: 20px;
}
.hero:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(20deg, #3498db, #2ecc71);
  background-size: 400% 400%;
  opacity: 0.4;
  animation: heroGradient 7s ease infinite;
}
.hero:after {
  content: '';
  position: absolute;
  right: 0px;
  bottom: 0px;
  left: 0px;
  width: 0;
  height: 0;
  border-bottom: 30px solid #f5f5f5;
  border-right: 100vw solid transparent;
}
.search {
  position: relative;
  z-index: 1;
  margin-top: -50px;
  margin-bottom: 30px;
}
.search__form {
  padding-top: 20px;
  padding-bottom: 20px;
}
.search__keywords,
.search__category,
.search__location {
  lost-column: 5/16 4 15px;
  margin-bottom: 0 !important;
  box-sizing: border-box;
  padding-left: 10px !important;
  outline: none;
  border: 1px solid rgba(0,0,0,0.1);
  transition: border-color 0.2s ease;
  color: rgba(0,0,0,0.6);
}
.search__keywords:hover,
.search__category:hover,
.search__location:hover {
  border-color: rgba(0,0,0,0.2);
}
.search__keywords:focus,
.search__category:focus,
.search__location:focus {
  border-color: rgba(0,0,0,0.4);
}
.search__keywords,
.search__category,
.search__location,
.search__submit {
  height: 44px;
  border-radius: 2px;
}
.search__submit {
  lost-column: 1/16 4 15px;
  font-size: 20px;
  justify-content: center;
}
.search__submit:after {
  content: '\f4a4';
  font-family: "ionicons";
}
.city-box {
  padding: 20px;
  padding-left: 10px;
  width: 100%;
  box-sizing: border-box;
  height: 377px;
}
.city-box__heading {
  margin: 0 0 10px;
  padding: 0 0 15px 10px;
  font-size: 16px;
  line-height: 16px;
  font-weight: 600;
  border-bottom: 1px solid rgba(0,0,0,0.08);
}
.city-box__list {
  padding-left: 0;
  list-style: none;
  margin-bottom: 0;
  font-size: 14px;
  line-height: 24px;
  font-weight: 600;
}
.city-box__anchor {
  display: flex;
  padding-right: 10px;
}
.city-box__anchor span:first-child {
  flex: 1;
}
.city-box__anchor span:last-child {
  font-weight: 400;
}
.city-box__anchor:before {
  content: '\f217';
  font-family: "ionicons";
  width: 20px;
  font-size: 9px;
  text-align: center;
  display: inline-block;
  opacity: 0;
  transition: opacity 0.1s ease-in-out;
}
.city-box__anchor:hover,
.city-box__anchor:focus {
  color: #3498db;
}
.city-box__anchor:hover:before {
  opacity: 1;
}
.city-box__item:last-child .city-box__anchor:before {
  content: '';
}
.city-box__item:last-child .city-box__anchor span:last-child {
  display: none;
}
.category-box .container {
  lost-center: auto 0;
}
.category-box__item {
  display: block;
  position: relative;
}
.category-box__item:hover .category-box__title {
  transform: translateY(0);
}
.category-box__item:hover .category-box__more {
  transform: translateY(0);
  opacity: 1;
}
.category-box__content {
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(70%, -50%);
}
.category-box__title {
  margin: 0;
  font-size: 35px;
  line-height: 35px;
  border-radius: 2px;
  font-weight: 500;
  color: #fff;
  text-shadow: 0px 0px 5px rgba(0,0,0,0.35);
  transform: translateY(15px);
  transition: transform 0.3s ease-out;
}
.category-box__more {
  margin: 0;
  text-align: right;
  font-size: 16px;
  list-style: font-size;
  color: #fff;
  text-shadow: 1px 1px 0px rgba(0,0,0,0.35);
  transform: translateY(-15px);
  opacity: 0;
  transition: all 0.3s ease-out;
}
.category-box__more:after {
  content: '\f363';
  font-family: "ionicons";
  margin-left: 5px;
}
.category-box__layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-repeat: no-repeat;
  transition: background-position 0.3s ease-out;
}
.category-box__item.vehicles {
  height: 250px;
  background: radial-gradient(#d68189, #c54d59);
}
.category-box__item.vehicles .category-box__border-2 {
  border-left: 0;
  margin-left: 120px;
}
.category-box__item.vehicles .category-box__layer {
  background-position: -120px 30px;
  background-size: 100%;
}
.category-box__item.vehicles:hover .category-box__layer {
  background-position: -140px 30px;
}
.category-box__item.electronics {
  height: 123px;
  margin-bottom: 4px;
  background: radial-gradient(#a4bcc2, #658c96);
}
.category-box__item.electronics .category-box__border-2 {
  border-top: 0;
  border-right: 0;
}
.category-box__item.electronics .category-box__layer {
  top: -10px;
  background-position: 100% 0;
  background-size: 50%;
}
.category-box__item.electronics:hover .category-box__layer {
  background-position: 110% 0;
}
.category-box__item.properties {
  height: 123px;
  background: radial-gradient(#69d2e7, #20aecb);
}
.category-box__item.properties .category-box__border-2 {
  display: none;
}
.category-box__item.properties .category-box__layer {
  background-position: right bottom;
  background-size: 100%;
}
.category-box__item.properties:hover .category-box__layer {
  background-position: 140% -40px;
}
.category-box__item.services {
  height: 123px;
  margin-top: 4px;
  background: radial-gradient(#c5cfc6, #839885);
}
.category-box__item.services .category-box__layer {
  background-position: 120% -20px;
  background-size: 70%;
}
.category-box__item.services:hover .category-box__layer {
  background-position: 120% -60px;
}
.category-box__large,
.category-box__small {
  lost-column: 1/2 2 4px;
}
.category-box__wide {
  lost-column: 1;
}
.category-box__wide .category-box__title,
.category-box__small .category-box__title {
  font-size: 25px;
  line-height: 25px;
}
.category-box__wide .category-box__more,
.category-box__small .category-box__more {
  text-align: left;
}
.category-box__wide .category-box__content,
.category-box__small .category-box__content {
  position: absolute;
  top: 50%;
  left: 25px;
  transform: translate(0, -50%);
}
.suggestion .slick-track {
  padding-top: 30px;
  padding-bottom: 30px;
}
.suggestion__heading {
  lost-column: 1;
  color: #666;
  margin: 0;
  padding: 20px 0 0;
  font-size: 20px;
  line-height: 20px;
  font-weight: 600;
  text-align: center;
}
.suggestion__heading:after {
  margin: 12px auto 0;
  content: '';
  display: block;
  background-color: #666;
  width: 60px;
  height: 2px;
  border-radius: 2px;
}
.suggestion__item {
  display: block;
  overflow: hidden;
  margin: 0 10px;
}
.suggestion__item:hover {
  box-shadow: 0 0 20px rgba(0,0,0,0.25);
}
.suggestion__thumb {
  position: relative;
  height: 150px;
  background-size: cover;
  background-position: center center;
  box-shadow: 0 0 20px rgba(0,0,0,0.1);
}
.suggestion__content {
  padding: 20px;
}
.suggestion__title {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  margin: 0 0 7px;
  font-size: 15px;
  line-height: 15px;
}
.suggestion__category {
  margin: 0;
  font-size: 11px;
  line-height: 11px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  color: #999;
}
.suggestion__footer {
  border-top: 1px solid rgba(0,0,0,0.1);
  margin-top: 20px;
  padding-top: 20px;
  display: flex;
  align-items: center;
}
.suggestion__price {
  flex: 1;
  font-size: 14px;
  line-height: 14px;
  font-weight: 800;
}
.suggestion__price:after {
  margin-left: 4px;
  content: 'DH';
}
.suggestion__color {
  background-color: #fff;
  width: 8px;
  height: 8px;
  display: inline-block;
  border-radius: 50%;
  margin-left: 5px;
  box-shadow: 0 0 4px rgba(0,0,0,0.25);
  transition: box-shadow 0.2s ease-in-out;
}
.suggestion__color:hover {
  box-shadow: 0 0 4px #000;
}
.suggestion__color.red {
  background-color: #ff4136;
}
.suggestion__color.yellow {
  background-color: #ffdc00;
}
.suggestion__color.black {
  background-color: #444;
}
.suggestion__color.blue {
  background-color: #0074d9;
}
.suggestion .slick-prev:before {
  content: '\f124';
  font-family: "ionicons";
}
.suggestion .slick-next:before {
  content: '\f125';
  font-family: "ionicons";
}
.suggestion .slick-prev:before,
.suggestion .slick-next:before {
  color: #777;
  transition: color 0.2s ease;
}
.suggestion .slick-prev:hover:before,
.suggestion .slick-next:hover:before {
  color: #222;
}
.application {
  position: relative;
  border-radius: 0px;
  background-image: url("../img/application.jpg");
  background-color: #000;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
}
.application:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0,0,0,0.5);
}
.application .container {
  position: relative;
  padding-top: 150px;
  padding-bottom: 150px;
  z-index: 99;
}
.application__heading {
  lost-column: 1 1;
  color: #fff;
  margin: 0 0 30px 0;
  font-size: 30px;
  font-weight: 900;
  text-align: center;
}
.application__buttons {
  lost-column: 1 1;
  text-align: center;
}
.application__link {
  display: inline-flex;
}
.application__link .content {
  padding: 0 15px;
  box-shadow: 0 0 30px rgba(0,0,0,0.07);
  border-radius: 0 4px 4px 0;
  background-color: #fff;
  height: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.application__link span {
  display: block;
  text-align: left;
  font-size: 14px;
  line-height: 17px;
}
.application__link span:last-child {
  font-weight: 800;
}
.application__link:before {
  flex: 1 0 50px;
  width: 50px;
  height: 50px;
  font-size: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  border-radius: 4px 0 0 4px;
  transition: all 0.2s ease-in-out;
}
.application__link.google {
  color: #36abbc;
}
.application__link.google:before {
  background-color: #36abbc;
  content: '\f2f0';
  font-family: "ionicons";
}
.application__link.google:hover,
.application__link.google:focus {
  color: #2e91a0;
}
.application__link.google:hover:before,
.application__link.google:focus:before {
  background-color: #2e91a0;
}
.application__link.apple {
  color: #646c6f;
  margin-left: 15px;
}
.application__link.apple:before {
  background-color: #646c6f;
  content: '\f227';
  font-family: "ionicons";
}
.application__link.apple:hover,
.application__link.apple:focus {
  color: #555c5e;
}
.application__link.apple:hover:before,
.application__link.apple:focus:before {
  background-color: #555c5e;
}
.footer {
  background-color: #fff;
  box-shadow: 0 0 30px rgba(0,0,0,0.07);
  padding-bottom: 80px;
}
.footer__primary {
  margin-bottom: 20px;
  padding: 20px 0;
  border-bottom: 1px solid rgba(0,0,0,0.1);
  align-items: center;
}
.footer__list {
  padding-left: 0;
  list-style: none;
  margin-bottom: 0;
  display: flex;
  font-size: 14px;
}
.footer__item {
  margin-right: 25px;
}
.footer__other {
  margin-bottom: 10px;
  font-size: 15px;
  line-height: 15px;
  color: #666;
}
.footer__other a {
  font-weight: 600;
  color: #555;
}
.footer__other a:hover,
.footer__other a:focus {
  color: #3498db;
}
.footer__link {
  font-size: 12px;
  line-height: 18px;
  float: left;
  color: #999;
}
.footer__link:hover,
.footer__link:focus {
  color: #3498db;
}
.footer__link:not(:last-child):after {
  content: ',';
  margin-right: 5px;
}
.footer .master__aside {
  display: flex;
  justify-content: flex-end;
}
.footer__social {
  display: inline-block;
  color: #fff;
  background-color: #bbb;
  margin-left: 4px;
  font-size: 14px;
  text-align: center;
  line-height: 27px;
  width: 27px;
  height: 27px;
  border-radius: 2px;
}
.footer__social:hover {
  color: #fff;
}
.footer__social.facebook:hover {
  background-color: #3b5998;
}
.footer__social.facebook:before {
  content: '\f231';
  font-family: "ionicons";
}
.footer__social.twitter:hover {
  background-color: #1da1f2;
}
.footer__social.twitter:before {
  content: '\f243';
  font-family: "ionicons";
}
.footer__social.youtube:hover {
  background-color: #cd201f;
}
.footer__social.youtube:before {
  content: '\f24d';
  font-family: "ionicons";
}
@-moz-keyframes heroGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@-webkit-keyframes heroGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@-o-keyframes heroGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes heroGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
